import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { AppointmentProfileComponent } from './appointment-profile.component';


const routes: Routes = [{
  path: '', component: AppointmentProfileComponent,
  children: [
    { path: 'add', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/appointment/appointment-create-profile/appointment-create-profile.module').then(m => m.AppointmentCreateProfileModule) },
    { path: 'list', canActivate: [ AuthGuard ], loadChildren: () => import('src/app/modules/admin/appointment/appointment-profile-list/appointment-profile-list.module').then(m => m.AppointmentProfileListModule) },
    { path: '', pathMatch : 'full', redirectTo: 'add' },
  ]
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AppointmentProfileRoutingModule { }
